import Axios from "axios";
const baseUrl = "http://localhost:9000";
const jobsUrl = `${baseUrl}/employer/jobs`;
import authHeader from "./authHeader";
const state = {
  jobs: []
};

const getters = {
  allJobs: state => state.jobs
};

const actions = {
  async fetchJobs(context) {
    context.commit(
      "setJobs",
      (await Axios.get(jobsUrl, { headers: authHeader() })).data
    );
  },
  async addUser(context, data) {
    context.commit("addUser", (await Axios.post(jobsUrl, data)).data);
  },
  async updateUser(context, updUser) {
    context.commit("updateUser", await Axios.put(`${jobsUrl}/${updUser._id}`));
  },
  async deleteUser(context, id) {
    context.commit("deleteUser", await Axios.delete(`${jobsUrl}/${id}`));
  }
  // async filterUser({commit}, e) {
  //   const limit = parseInt(e.target.options[e.target.options.selectedIndex].innerText);
  //   console.log(limit)
  //   console.log(e)
  //   // context.commit("deleteUser", await Axios.delete(`${usersUrl}/${id}`));
  // }
};

const mutations = {
  setJobs: (state, jobs) => (state.jobs = jobs),
  addUser: (state, todo) => state.todos.unshift(todo),
  deleteUser: (state, id) =>
    (state.todos = state.todos.filter(todo => todo._id !== id))
};

export default {
  state,
  getters,
  actions,
  mutations
};
